.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
  z-index: 99;
}

.image {
  width: 20rem;
}

.wrongs {
  font-size: 21px;
  font-weight: 600;
  text-align: center;
}

.content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 4em;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0px 0px 16px 5px rgba(#6ac3de, 0.35);
}