.app-wrapper {
    height: 100vh;
    max-width: 1920px;
    background-image: url(/rolling-scopes-school/youjob13-JSFE2021Q1/tree/gh-pages/static/media/main-bg.a2219b80.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.app-content {
    height: calc(100% - 224px);
    padding: 1rem 14em;
}

@media (max-width: 1440px) {
    .app-content {
        height: calc(100% - 164px);
        padding: 1rem 6em;
    }

    .app-wrapper {
        max-width: 1440px;
    }
}

@media (max-width: 1200px) {
    .app-wrapper {
        height: auto;
        max-width: 1200px;
    }

    .app-content {
        padding: 1rem 4em;
    }
}

@media (max-width: 992px) {
    .app-wrapper {
        max-width: 992px;
    }
}

@media (max-width: 768px) {
    .app-wrapper {
    }
}


button {
    border: none;
}

button:focus {
    outline: none;
}

h1, h2, h3, h4, h5, h6, ul, li, p, span, button, a, input, div {
    margin: 0;
    padding: 0;
}

li {
